.header {
    height: 40px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;

    .logo-container {
      height: 100%;
      width: 70px;
      padding: 25px;
    }

    .options {
      width: 50%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      cursor: pointer;

      .option {
        padding: 10px 15px;
      }
    }
  }
  form {
    padding: 5px;
    width: 100%;
    background-color: #0000b3;
  }
